import './style.css';
import { Map, VectorRenderTile, View } from 'ol';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import OSM from 'ol/source/OSM';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import chroma from "chroma-js";
import Style from 'ol/style/Style';
import Fill from 'ol/style/Fill';
import Stroke from 'ol/style/Stroke';
import { fromLonLat } from 'ol/proj';
import * as d3 from 'd3';

const map = new Map({
  target: 'map',
  layers: [
    new TileLayer({
      source: new OSM()
    })
  ],
  view: new View({
    // projection: "EPSG:4326",
    center: fromLonLat([12.68, 42.0]),
    zoom: 6
  })
});

//gettin inital week number 
let mydate = document.getElementById('range-slider').value;

const vectorFile = new VectorLayer({
  source: new VectorSource({
    url: './data/mead_egg_abundance.geojson',
    format: new GeoJSON(),
  }),
  map: map,
  style: function (feature) {
    let atrribute = feature.get('w_' + Number(mydate));
    let fillcolor = brewColors(atrribute);
    return new Style({
      fill: new Fill({ color: fillcolor }),
      stroke: new Stroke({
        color: '#000000',
        width: 0.5
      })
    })
  }
})

// feature Overlay
// visual properties of the overlayin feature 
const featureOverlay = new VectorLayer({
  source: new VectorSource(),
  map: map,
  style: {
    'stroke-color': 'rgba(255,255,255,0.7)',
    'stroke-width': 3,
  }
});

// logic of the hightlight mechanism 
let currentFeature;
const displayFeatureInfo = function (pixel) {
  const feature = map.forEachFeatureAtPixel(pixel, function (feature) {
    return feature;
  });
  const info = document.getElementById('info');
  info.style.left = pixel[0] + 20 + 'px';
  info.style.top = pixel[1] + 'px';
  if (feature !== currentFeature) {
    if (currentFeature) {
      featureOverlay.getSource().removeFeature(currentFeature);
      info.innerHTML = ''
      info.style.visibility = 'hidden'
    };
    if (feature) {
      featureOverlay.getSource().addFeature(feature);
      info.style.visibility = 'visible'
      generateGraph(feature, mydate)
    };
    currentFeature = feature
  }
}

// cursor and map events
map.on('pointermove', function (event) {
  if (event.dragging) {
    currentFeature = undefined;
    return;
  };
  const pixel = map.getEventPixel(event.originalEvent);
  displayFeatureInfo(pixel, event.originalEvent.target);
});

map.on('click', function (event) {
  displayFeatureInfo(event.pixel)
});

// getting metadata from features
const getMetadata = function (feature) {
  const weeks = Array(52).fill().map((element, index) => index + 1);
  const values = weeks.map((element) => feature.get('w_' + Number(element)));
  return { 'weeks': weeks, 'values': values }
};

//setting up a color scale generator based on chroma-js
const brewColors = function (value) {
  const colorFunk = chroma.scale('YlOrRd').colors(7);
  // return(colorFunk(value/500));
  if (value <= 10) {
    return chroma(colorFunk[0]).alpha(0.7)
  } else if (value <= 25) {
    return chroma(colorFunk[1]).alpha(0.7)
  } else if (value <= 50) {
    return chroma(colorFunk[2]).alpha(0.7)
  } else if (value <= 100) {
    return chroma(colorFunk[3]).alpha(0.7)
  } else if (value <= 200) {
    return chroma(colorFunk[4]).alpha(0.7)
  } else if (value <= 400) {
    return chroma(colorFunk[5]).alpha(0.7)
  } else {
    return chroma(colorFunk[6]).alpha(0.7)
  }
};

const generateGraph = function (feature, mydate) {
  // access the data
  const data = getMetadata(feature);

  const margin = { top: 30, right: 10, bottom: 30, left: 50 }
  const width = 350 - margin.left - margin.right
  const height = 150 - margin.top - margin.bottom

  const svg = d3.select('#info')
    .append('svg')
    .attr('width', width + margin.left + margin.right)
    .attr('height', height + margin.bottom + margin.top)
    .append('g')
    .attr("transform", `translate(${margin.left},${margin.top})`);

  const xScale = d3.scaleLinear()
    .domain([1, 52])
    .nice()
    .range([0, width]);

  const yScale = d3.scaleLinear()
    .domain([0, 250])
    .range([height, 0]);

  const xAxis = d3.axisBottom(xScale)
    .ticks(14);
  const yAxis = d3.axisLeft(yScale)
    .ticks(5);

  svg.append('g')
    .attr('class', 'x-axis')
    .attr('transform', `translate(0, ${height})`)
    .call(xAxis);

  svg.append('g')
    .attr('class', 'y-axis')
    .call(yAxis);

  const line = d3.line()
    .x((d, i) => xScale(data.weeks[i]))
    .y(d => yScale(d))
    .curve(d3.curveMonotoneX)

  let udata = [mydate, mydate]
  let kdata = [0, 250]
  const line_date = d3.line()
    .x((d,i) => xScale(udata[i]))
    .y((d,i) => yScale(kdata[i]))
    .curve(d3.curveMonotoneX)

  svg.append('path')
    .datum(data.values)
    .attr('class', 'line')
    .attr('d', line_date)
    .style('fill', 'none') //remove the are under the line
    .style('stroke', 'red') // set the line color
    .style('stroke-width', 2) //set the line thickness

  svg.append('path')
    .datum(data.values)
    .attr('class', 'line')
    .attr('d', line)
    .style('fill', 'none') //remove the are under the line
    .style('stroke', 'blue') // set the line color
    .style('stroke-width', 3) //set the line thickness

  // x axis labels
  svg.append("text")
    .attr('class', 'x label')
    .attr('text-anchor', 'end')
    .attr('x', width / 2 + margin.left / 2)
    .attr('y', height + margin.top)
    .attr('font-size', '12px')
    .text('weeks')
  // y axis labels
  svg.append("text")
    .attr('class', 'y label')
    .attr('text-anchor', 'end')
    .attr('x', 0 - (margin.bottom - margin.top- margin.bottom) / 2)
    .attr('y', 0 - (margin.left + margin.right) / 2)
    .attr('font-size', '12px')
    .attr('transform', 'rotate(-90)')
    .text('Median number of eggs')
  // TITLE
  svg.append("text")
    .attr("x", (width / 2))
    .attr("y", 0 - (margin.top / 2))
    .attr("text-anchor", "middle")
    .attr("font-size", "14px")
    .text(`${feature.get("NAME_1")} (Weeks:${mydate})`)

  svg.append("text")
    .attr("x", (width / 2))
    .attr("y", 0 - (margin.top / 2) / 4)
    .attr("text-anchor", "middle")
    .attr("font-size", "14px")
    .text(`Median eggs abundance ${feature.get("w_" + Number(mydate))}`)
  return svg
}

// reactive slider and change style of features
let currentTitle = document.getElementById('title')
let sliderValue = document.getElementById("range-slider")
const update = function () {
  const source = vectorFile.getSource()
  const features = source.getFeatures();
  features.map((feature) => {
    var style = new Style({
      fill: new Fill({ color: brewColors(feature.get("w_" + Number(sliderValue.value))) }),
      stroke: new Stroke({ color: '#000', width: 0.5 })
    })
    feature.setStyle(style)
  })
  mydate = sliderValue.value
  currentTitle.innerHTML = "Weeks: " + Number(sliderValue.value)
  console.log(currentTitle.innerHTML)
}

sliderValue.addEventListener('input', update)